.wp-block-columns, .wp-block-group {
  max-width: 1340px;
  margin: auto;
}

p {
  font: Objektiv VF, sans-serif;
  color: #F7F7F7;
}

h2.wp-block-heading {
  font: Objektiv VF, sans-serif;
  color: #F7F7F7;
}

.hmax {
  text-transform: uppercase;
  font-weight: 800;
}
.hmax .last-word {
  color: #A49E93;
  display: table;
}

.inner-max-width {
  margin: auto;
  width: 100%;
  max-width: 1330px;
}